import React from 'react';

import { ItemContainer, ItemInfo, ItemImage, Title, Text } from './styles';

const ResidentItem = ({ resident }) => {
  return (
    <ItemContainer>
      <ItemImage>
        <img src={resident.image} alt={resident.type} />
      </ItemImage>

      <ItemInfo>
        <>
          <Title>{resident.name}</Title>
          <Text>{resident.type}</Text>
          <Text>{resident.type}</Text>
        </>
      </ItemInfo>
    </ItemContainer>
  );
};

export default ResidentItem;

